code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.floor-map-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100vw;
}

.floor-map-wrapper {
    position: relative;
    width: 100%;
    height: 100%;
}

.floor-map-image {
    width: 100%;
    height: auto;
    /* object-fit: contain; Ensure the image scales correctly within its container */
}

.student-container {
    position: absolute;
    transform: translate(-50%, -50%);
    /* cursor: pointer; Change cursor to pointer on hover */
    overflow: visible;
}

.student {
    width: 5vw; /* Size of the circle relative to the parent container's width */
    aspect-ratio: 1 / 1; /* Ensures the height is always equal to the width */
    background-color: red;
    border-radius: 50%;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2.5vw; /* Font size relative to the viewport width */
}

.info-box {
    display: flex;
    /* flex-wrap: wrap; */
    width: 15vw; 
    height: 25vh;
    padding: .5vw;
    background-color: white;
    box-shadow: 0 0 1vw rgba(245, 245, 220, 0.5)
}

.left-column {
    flex: 2;
    margin-right: .25vw;
}

.right-column {
    flex: 6;
}

.portrait-image {
    width: 100%;
    aspect-ratio: 1 / 1.2;
    object-fit: cover;
}

.artwork-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.info-box-text {
    font-size: .6vw;
    font-weight: bold;
}

.social-icon {
    width: 10px;
    height: 10px;
}
